.sec {
  background-color: rgba(255, 255, 255, 1);
  border-radius: 0.25rem;
  box-shadow: 3px 3px rgba(0, 0, 0, 0.1);
}

.main-feed {
  background-color: rgba(95, 143, 80, 0.15);
  font-family: 'Neuton', serif;
}
h3 {
  font-size: 2.5rem;
  font-weight: 500;
  line-height: 2rem;
  margin-bottom: 1.3rem;
}
input[type='text'] {
  font-size: 1.1rem;
  vertical-align: middle;
}
.btn {
  font-size: 1.3rem;
  font-weight: 500;
  padding: 0;
  text-align: center;
  padding-bottom: 0.25rem;
}
label {
  font-size: 1.2rem;
}
p {
  font-size: 1rem;
  line-height: 1.2rem;
  font-weight: 500;
}
img {
  width: 100%;
  height: auto;
}
.map-responsive {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  height: 0;
}
.map-responsive iframe {
  text-align: center;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}
.form-control {
  margin: 1px;
}

.cur {
  cursor: pointer;
}

.no-click {
  cursor: default !important;
  pointer-events: all !important;
}

.person-form {
  background-color: rgba(115, 163, 100, 0.1);
  border-radius: 0.25rem;
  border: 1px solid rgba(151, 151, 151, 0.5);
  box-shadow: 2px 2px rgba(0, 0, 0, 0.1);
}

